.inner-nav-wrap {
  margin-top: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1.5px solid #992369;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .inner-nav-wrap .logo img {
    width: 150px; }
  .inner-nav-wrap nav ul {
    display: flex;
    justify-content: space-between; }
    .inner-nav-wrap nav ul li {
      margin-left: 0.8rem;
      font-size: 0.8rem;
      list-style: none; }
      .inner-nav-wrap nav ul li:last-child {
        margin-right: 0.5rem; }
      .inner-nav-wrap nav ul li a:hover {
        color: #f69331;
        font-weight: 700;
        transition: all 300ms ease-in-out; }

.info-banner {
  margin: 7rem 0 3rem 0;
  border-top: 1.5px solid #992369;
  padding-top: 3rem; }
  .info-banner .banner-wrapper .banner-container {
    display: flex;
    justify-content: space-between;
    margin: 0 0.9rem; }
    .info-banner .banner-wrapper .banner-container .banner-element {
      width: 22vw; }
      .info-banner .banner-wrapper .banner-container .banner-element .heading {
        display: flex;
        align-items: center;
        margin: 0; }
        .info-banner .banner-wrapper .banner-container .banner-element .heading h3 {
          font-size: 1.3rem;
          margin: 0 0 0 0.8rem; }
        .info-banner .banner-wrapper .banner-container .banner-element .heading img {
          margin: 0;
          width: 1.2rem; }
      .info-banner .banner-wrapper .banner-container .banner-element p {
        font-weight: lighter;
        font-size: 0.7rem; }
        .info-banner .banner-wrapper .banner-container .banner-element p span a {
          color: #992369; }

footer {
  width: 100%;
  height: 10rem;
  background: #f69331; }
  footer .footer-container {
    width: 80%;
    margin: 0 auto;
    padding-top: 1.5rem; }
    footer .footer-container .footer-wrapper .map nav ul li {
      list-style: none;
      font-size: 0.7rem;
      margin-top: 0.5rem; }
      footer .footer-container .footer-wrapper .map nav ul li a {
        color: white; }

.carousel-setup {
  display: flex;
  align-items: center;
  margin: 4rem 0; }
  .carousel-setup .carousel-container {
    width: 65vw;
    overflow: hidden;
    margin: 2rem auto 0 auto; }
    .carousel-setup .carousel-container .carousel {
      display: flex;
      width: 100%;
      height: 100%; }
      .carousel-setup .carousel-container .carousel .slide img {
        width: 65vw; }
      .carousel-setup .carousel-container .carousel .slide .description {
        font-weight: lighter;
        font-size: 0.7rem; }
  .carousel-setup button {
    border: none;
    padding: 0.5rem;
    text-decoration: none;
    background: none;
    font-size: 1.5rem;
    font-weight: lighter;
    color: #f69331; }
    .carousel-setup button:hover {
      color: #992369; }

.aboutus {
  color: white;
  background: #992369;
  padding: 1rem 0; }
  .aboutus .aboutus__inner .heading {
    display: flex;
    align-items: center; }
    .aboutus .aboutus__inner .heading .icon__white img {
      width: 40px; }
    .aboutus .aboutus__inner .heading h3 {
      margin-left: 0.5rem;
      font-size: 2.5rem; }
  .aboutus .aboutus__inner p {
    margin: 0;
    font-size: 1.6rem;
    font-weight: lighter; }

body {
  font-size: 62.5%;
  font-family: helvetic, sans-serif;
  margin: 0; }
  body a {
    text-decoration: none;
    color: black;
    cursor: pointer; }
  body .wrapper {
    width: 80%;
    margin: 0 auto; }
